      >
        <h2 :key="1001" v-if="scrollPercentage >= 1">What is Good Code?</h2>
        <div :key="1002" class="showcase" v-if="scrollPercentage >= 1.5">
          <div class="HCI-string" @click="switchAcitvedString">
            <span v-if="this.activedStringIndex === 0"
              >HCI, the abbreviations of &nbsp;</span
            >
            <transition-group name="switch-group">
              <div
                class="switch-group-item"
