          ></span
        >
        <span :key="1006" v-if="scrollPercentage >= 5.5"
          >3. Engagingness<span class="hint" v-if="showHint[2]"
            >酷不酷,想不想学?</span
          ></span
        >
        <span :key="1007" v-if="scrollPercentage >= 6"
          >4. Error Tolerance<span class="hint" id="hint4" v-if="showHint[3]"
            >console一片红?</span
          ></span
        >
        <span :key="1008" v-if="scrollPercentage >= 6.5"
