<script>
export default {
  data() {
    return {
      HCIString0: [
        ["Human-Computer-Interaction", "Human-Code-Interaction"],
        ["TOM　MARVOLO　RIDDLE", "I　AM　LORD　VOLDEMORT"],
      ],
      activedStringIndex: 0,
      bindKeyType: "item",
      bindKeyTypeList: ["key", "item", "item+key", "key+random"],
      bindKeyTypeShowList: false,
      scrollTop: 0,
      windowHeight: 1,
      hitBottom: false,
      stringReverse: true,
    };
  },
  computed: {
    scrollPercentage() {
      if (this.hitBottom) return 10;
      return this.scrollTop / this.windowHeight;
    },
    stringChange() {
      return this.scrollTop / this.windowHeight > 2.5;
    },
    HCIString() {
      // eslint-disable-next-line no-debugger
      return this.stringReverse === this.stringChange
        ? this.HCIString0[this.activedStringIndex][0].split("")
        : this.HCIString0[this.activedStringIndex][1].split("");
    },
    showHint() {
      return new Array(5)
        .fill()
        .map((e, i) => this.scrollPercentage > 1 + 0.1 * i);
    },
  },
  mounted() {
    if (this.$refs.body) {
      this.$refs.body.addEventListener("scroll", () => {
        this.scrollTop = this.$refs.body.scrollTop;
        if (this.scrollTop / this.windowHeight > 9) this.hitBottom = true;
      });
    }
    if (document.body) {
      this.windowHeight = document.body.clientHeight;
    }
  },
  methods: {
    switchAcitvedString() {
      if (this.activedStringIndex === 0) {
        this.activedStringIndex = 1;
        return;
      }
      this.activedStringIndex = 0;
    },
    scrollDown() {
      if (this.stringReverse) {
        this.stringReverse = false;
        return;
      }
      this.stringReverse = true;
    },
    getKey(item, key) {
      switch (this.bindKeyType) {
        case "key":
          return key;
        case "item":
          return item + this.getAppearIndex(item, key);
        case "item+key":
          return item + key;
        case "key+random":
          return key + Math.random();
        default:
          return key;
      }
    },
    getAppearIndex(item, index) {
      let count = 0;
      for (let i = 0; i < index; i++) {
        if (this.HCIString[i] === item) count++;
      }
      return count;
    },
  },
};
</script>